<template>
  <div>
    <!-- Header with logo and Vessential branding -->
    <header class="app-header">
      <div class="brand-container">
        <img src="@/assets/logo.png" alt="Vessential Logo" class="logo" />
        <h1 class="brand-name">Vessential</h1>
      </div>
      <div class="tabs">
        <button @click="activeTab = 'finance'" :class="{ active: activeTab === 'finance' }">Finance</button>
        <button @click="activeTab = 'inventory'" :class="{ active: activeTab === 'inventory' }">Inventory</button>
        <button @click="showAddForm = !showAddForm" class="add-btn">+ Add Item</button>
      </div>
    </header>

    <!-- Sorting Options for Inventory Tab -->
    <div v-if="activeTab === 'inventory'" class="sorting-options">
      <label>Sort By: </label>
      <select v-model="sortOrder" @change="sortItems">
        <option value="az">A-Z</option>
        <option value="za">Z-A</option>
      </select>
    </div>

    <!-- Finance Tab Content -->
    <div v-if="activeTab === 'finance'" class="finance-container">
      <h2>Finance Overview</h2>
      <p>Placeholder for financial summaries, profit calculations, and expense tracking.</p>
    </div>

    <!-- Inventory Tab: Displays the inventory list with profit and totals -->
    <div v-if="activeTab === 'inventory'" class="inventory-container">
      <h2>Inventory List</h2>
      <table class="inventory-table">
        <thead>
          <tr>
            <th>Item</th>
            <th>Category</th>
            <th>Quantity</th>
            <th>Cost</th>
            <th>Sell</th>
            <th>Profit</th>
            <th>Margin</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in items" :key="index">
            <td>{{ item.name }}</td>
            <td>{{ item.category }}</td>
            <td>{{ item.quantity }}</td>
            <td>${{ formatCurrency(item.cost) }}</td>
            <td>${{ formatCurrency(item.sellPrice) }}</td>
            <td :class="{'profit-positive': calculateProfit(item) >= 0, 'profit-negative': calculateProfit(item) < 0}">
              ${{ formatCurrency(calculateProfit(item)) }}
            </td>
            <td :class="{'profit-positive': calculateProfit(item) >= 0, 'profit-negative': calculateProfit(item) < 0}">
              {{ calculateProfitMargin(item) }}%
            </td>
            <td>
              <button class="edit-btn" @click="editItem(index)">Edit</button>
              <button class="delete-btn" @click="deleteItem(index)">Delete</button>
            </td>
          </tr>
        </tbody>
        <tfoot>
          <tr>
            <td colspan="3"><strong>Total</strong></td>
            <td>${{ formatCurrency(totalCost) }}</td>
            <td>${{ formatCurrency(totalSell) }}</td>
            <td>${{ formatCurrency(totalProfit) }}</td>
            <td></td>
            <td></td>
          </tr>
        </tfoot>
      </table>
    </div>

    <!-- Add New Item Form as a popup -->
    <div v-if="showAddForm" class="add-item-popup">
      <h3>Add New Item to Inventory</h3>
      <form @submit.prevent="addItem" class="add-item-form">
        <input v-model="newItem.name" placeholder="Item Name" required />
        <input v-model="newItem.quantity" type="number" placeholder="Quantity" required />
        <input v-model="newItem.category" placeholder="Category" required />
        <input v-model="newItem.cost" type="number" placeholder="Cost" required />
        <input v-model="newItem.sellPrice" type="number" placeholder="List Price" required />
        <button type="submit" class="add-btn">Add Item</button>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      activeTab: 'inventory',
      showAddForm: false,
      sortOrder: 'az',
      items: [],
      newItem: {
        name: '',
        quantity: '',
        category: '',
        cost: '',
        sellPrice: '',
      },
      editingIndex: null
    };
  },
  computed: {
    totalCost() {
      return this.items.reduce((sum, item) => sum + (item.cost || 0) * item.quantity, 0);
    },
    totalSell() {
      return this.items.reduce((sum, item) => sum + (item.sellPrice || 0) * item.quantity, 0);
    },
    totalProfit() {
      return this.items.reduce((sum, item) => sum + this.calculateProfit(item), 0);
    }
  },
  methods: {
    formatCurrency(value) {
      return parseFloat(value).toFixed(2);
    },
    calculateProfit(item) {
      const FVF_RATE = 0.1235; // 12.35%
      const TRANSACTION_FEE = 0.40;

      const sellPrice = item.sellPrice * item.quantity;
      const cost = item.cost * item.quantity;
      const ebayFee = (sellPrice * FVF_RATE) + TRANSACTION_FEE;
      const profit = sellPrice - cost - ebayFee;

      return profit;
    },
    calculateProfitMargin(item) {
      const profit = this.calculateProfit(item);
      const totalSellPrice = item.sellPrice * item.quantity;
      const margin = (profit / totalSellPrice) * 100;

      return margin.toFixed(2);
    },
    addItem() {
      if (this.editingIndex !== null) {
        this.items.splice(this.editingIndex, 1, { ...this.newItem });
        this.editingIndex = null;
      } else {
        this.items.push({ ...this.newItem });
      }
      this.resetForm();
      this.saveItems();
      this.showAddForm = false; // Close the form after adding
    },
    editItem(index) {
      this.newItem = { ...this.items[index] };
      this.editingIndex = index;
      this.showAddForm = true;
    },
    deleteItem(index) {
      this.items.splice(index, 1);
      this.saveItems();
    },
    saveItems() {
      localStorage.setItem('inventory-items', JSON.stringify(this.items));
    },
    loadItems() {
      const savedItems = localStorage.getItem('inventory-items');
      if (savedItems) {
        this.items = JSON.parse(savedItems);
      }
    },
    resetForm() {
      this.newItem = { name: '', quantity: '', category: '', cost: '', sellPrice: '' };
    },
    sortItems() {
      if (this.sortOrder === 'az') {
        this.items.sort((a, b) => a.name.localeCompare(b.name));
      } else {
        this.items.sort((a, b) => b.name.localeCompare(a.name));
      }
    }
  },
  created() {
    this.loadItems();
  }
};
</script>

